/* Contêiner principal */
.userManagementContainer {
  display: flex;
  height: 100vh;
  background-color: #f4f4f4;
}

/* Estilo da Sidebar */
.sidebar {
  width: 250px;
  background-color: #222;
  color: white;
  padding: 20px;
  box-shadow: 2px 0 10px rgba(0, 0, 0, 0.1);
}

/* Conteúdo principal */
.userManagementContent {
  flex: 1;
  padding: 40px;
  background-color: #fff;
  color: #333;
}

/* Título da página */
.userManagementTitle {
  font-size: 28px;
  font-weight: 600;
  margin-bottom: 20px;
  border-bottom: 2px solid #e0e0e0;
  padding-bottom: 10px;
}

/* Mensagem de erro */
.errorMessage {
  color: red;
  margin-bottom: 20px;
}

/* Texto de carregamento */
.loadingText {
  font-size: 1.2rem;
  color: #666;
}

/* Lista de usuários */
.userList {
  list-style: none;
  padding: 0;
}

/* Item de usuário */
.userItem {
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 15px;
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.userItemStrong {
  font-size: 1.2rem;
}

.userItemEm {
  font-style: italic;
}

/* Ações do usuário */
.userActions {
  display: flex;
  gap: 10px;
}

.editButton,
.deleteButton {
  padding: 5px 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.editButton {
  background-color: #007bff;
  color: white;
}

.editButton:hover {
  background-color: #0056b3;
}

.deleteButton {
  background-color: #dc3545;
  color: white;
}

.deleteButton:hover {
  background-color: #c82333;
}

/* Formulário de usuário */
.userForm {
  margin-bottom: 20px;
}

.userFormTitle {
  font-size: 1.5rem;
  margin-bottom: 10px;
}

.userFormInput,
.userFormSelect {
  display: block;
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  box-sizing: border-box;
}

.addButton {
  background-color: #28a745;
  color: white;
  padding: 10px 20px;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  font-size: 1.1rem;
  transition: background-color 0.3s ease;
}

.addButton:hover {
  background-color: #218838;
}